import React from 'react';
import { Link } from 'react-router-dom';
import { Link as ScrollLink } from 'react-scroll';

const Header = () => {
  return (
    <header>
      <nav>
        <div className="filter-box">
          <ul className="menu">
            <li><Link to="/"></Link></li>
            <li><ScrollLink to="sxetika" smooth={true} duration={500}></ScrollLink></li>
            <li><ScrollLink to="MapComponent" smooth={true} duration={500}></ScrollLink></li>
            <li><ScrollLink to="data-container" smooth={true} duration={500}> </ScrollLink></li>
            <li><ScrollLink to="epikoinwnia" smooth={true} duration={500}></ScrollLink></li>
            <li><Link to="/add"> </Link></li>
          </ul>
        </div>
      </nav>
    </header>
  );
};

export default Header;
